













































import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import {IBookingAvailability, IPayment, ISubscription, ITrainerProfile, TrainingSessionDuration, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import RequestRadio from "@/components/RequestRadio.vue"
import CardPayment from "@/components/CardPayment.vue"
import NotificationMessage from "@/components/NotificationMessage.vue"
import i18n from "../i18n"
import { TranslateResult } from "vue-i18n"

@Component({ components: { CardPayment, NotificationMessage, RequestRadio } })
export default class TrainSpecific extends Vue {
	@Prop()
	private trainer!: ITrainerProfile

	private slide: number = 1
	private systemTime: string = ""

	private durations: TrainingSessionDuration[] = [30, 60]
	private types: TrainingSessionType[] = []
	private requestDuration: TrainingSessionDuration = this.durations[0]
	private requestType: TrainingSessionType = 1
	private isSubscribed: ISubscription | null = null
	private typeDesc: object = {
		hatha: i18n.t("hatha"),
		ashtanga: i18n.t("astanga"),
		vinyasa: i18n.t("vinayasa"),
		freeform: i18n.t("freeform"),
		yin: i18n.t("yin")
	}

	private avatar: string | null = null
	private trainerName: string | null = null
	private sessionTickets: number = 0
	private daysInWeek: TranslateResult[] = [i18n.t("sunday"), i18n.t("monday"),
		i18n.t("tuesday"), i18n.t("wednesday"), i18n.t("thursday"),
		i18n.t("friday"), i18n.t("saturday")]
	private days: TranslateResult[] = []
	private hours: IBookingAvailability[] = []
	private availableDates: number[] = []
	private booking: IBookingAvailability | null = null

	private message: string | null = null
	public created(): void {
		this.getSubscription()
		this.getSessionTickets()
	}
	private notifyCardErr(err: string) {
		this.slide = 1
		this.message = err
	}
	private getSessionTickets(): void {
		Portal.client.call.sessionTickets.get()
		.response
		.then(res => {
			this.sessionTickets = res.length
		})
		.catch(reason => {})
	}
	private getSubscription(): void {
		Portal.client.call.subscriptions.get()
		.response
		.then(res => {
			this.isSubscribed = res
			})
		}
	private mounted(): void {
		this.avatar = this.trainer.ProfilePictureUrl || `${require("@/assets/images/avatar-dummy.jpg")}`
		this.trainerName = `${this.trainer.FirstName} ${this.trainer.LastName}`
		this.types = this.trainer.TrainingTypes.sort()
		this.requestType = this.requestType && this.types.indexOf(this.requestType) !== -1
			? this.requestType
			: this.types[0]
	}

	/**
	 * Get system time
	 */

	private getSystemTime(): Promise<string> {
		return Portal.client.call.system.Time()
			.response
			.then(res => res.Time)
	}

	/**
	 * Set booking termin
	 */

	private setBooking(booking: IBookingAvailability): void {
		this.booking = booking
		this.slide = 3

	}

	private goBackToType(): void {
		this.$emit("close")
	}

	/**
	 * Open Slide 2: Trainer available time slots
	 */

	private bookingAvailability(): void {
		if (this.isSubscribed === undefined && this.sessionTickets === 0 || this.isSubscribed === null && this.sessionTickets === 0)
			this.$emit("openPlan")
		else
				Portal.client.call.trainer.availability(this.trainer.Id, this.requestDuration)
					.response
					.then(result => this.populateDaysHours(result))
					.then(() => {
						this.getSystemTime()
							.then(res => {
								this.systemTime = res
								this.slide = 2
							})
					})
					.catch(reason => {
						throw reason
					})
		}

	/**
	 * Booking POST
	 */

	private postBooking(): void {
		const booking: IBookingAvailability = this.booking as IBookingAvailability
		Portal.client.call.booking.specific(this.trainer.Id, booking.Id, this.requestType, this.requestDuration)
			.response
			.then(() => {
				this.$emit("modal", this.requestDuration, this.requestType, this.formatTermin(booking), this.trainer.FirstName)
				this.$emit("close")
			})
			.catch(reason => {
				this.$emit("modalErr", reason)
				throw reason
			})
	}

	// format termin string for modal box
	private formatTermin(booking: IBookingAvailability): string {
		const hour = this.setHour(booking)
		let day = this.getDayLabel(booking.TimeSlot.Start)
		if ([i18n.t("today"), i18n.t("tomorrow")].indexOf(day.toLowerCase()) !== -1)
			day = day.toLowerCase()
		return `${day} at ${hour}`
	}

	/**
	 * Populate days and hours arrays
	 */

	private populateDaysHours(slots: IBookingAvailability[]) {
		this.hours = slots
		slots.forEach(el => {
			const day = new Date(el.TimeSlot.Start).getDate()
			if (this.availableDates.indexOf(day) === -1) {
				this.availableDates.push(day)
				this.days.push(el.TimeSlot.Start)
			}
		})
		this.days.sort()
	}

	/**
	 * @returns day label i.e. Today, Tomorrow, Friday...
	 */

	private getDayLabel(day: string): string {
		const date = new Date(day)
		const today = new Date()
		const tomorrow = new Date()
		tomorrow.setDate(today.getDate() + 1)

		if (date.getDate() === today.getDate())
			return i18n.t("today").toString()
		else if (date.getDate() === tomorrow.getDate())
			return i18n.t("tomorrow").toString()
		return `${this.daysInWeek[new Date(day).getDay()]} ${new Date(day).getMonth() + 1}/${new Date(day).getDate()}`
	}

	/**
	 * Check if hour exists for particular day
	 */

	private checkDay(day: string, hour: IBookingAvailability): boolean {
		const d1 = new Date(day)
		const d2 = new Date(hour.TimeSlot.Start)

		if (d1.getDay() === new Date(this.systemTime).getDay()) {
			const minOffset = new Date(this.systemTime)
			minOffset.setMinutes(minOffset.getMinutes() + 10)
			return d1.getDate() === d2.getDate() && d2 > minOffset
		}
		return d1.getDate() === d2.getDate()
	}

	/**
	 * returns i.e. 10am or 1:30pm
	 */

	private setHour(hour: IBookingAvailability): string {
		return this.amPm(new Date(hour.TimeSlot.Start).getHours(), new Date(hour.TimeSlot.Start).getMinutes())
	}

	/**
	 * Convert hour in am/pm format
	 */

	private amPm(hour: number, min: number): string {
		const m = (min && min < 10) ? this.leadZero(min) : min
		if (hour > 12)
			return `${hour % 12}${m ? ":" + m : ""}pm`
		else if (hour < 12)
			return `${hour}${m ? ":" + m : ""}am`
		return `12${m ? ":" + m : ""}pm`
	}

	/**
	 * Lead zero
	 */

	private leadZero = (x: number): string => x < 10 ? x < 0 ? `00` :	`0${x}` : `${x}`

	/**
	 * Training type name
	 */

	private whichType(typeId: number): string {
		return TrainingSessionType[typeId]
	}

	/**
	 * Capitalize word
	 */

	private capitalize(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1)
	}
}
